.task-container {
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 20px;
  }
  
  .header-container {
    flex-wrap: nowrap;
  }
  
  .add-task-button {
    border-radius: 4px;
    padding: 8px 16px;
    font-size: 1rem;
  }

  
  @media (max-width: 576px) {
    .header-container {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  
    .add-task-button {
      white-space: nowrap;
    }
  
    .task-container {
      padding: 10px;
    }
  
    .task-item h5 {
      font-size: 1rem;
    }
  
    .task-item p {
      font-size: 0.9rem;
    }
  }

  .skeleton-title {
    width: 200px;
    height: 24px;
    border-radius: 4px;
    background: linear-gradient(90deg, var(--color-shimmer-light) 25%, var(--color-shimmer-dark) 50%, var(--color-shimmer-light) 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
  }
  
  .skeleton-button {
    width: 100px;
    height: 40px;
    border-radius: 4px;
    background: linear-gradient(90deg, var(--color-shimmer-light) 25%, var(--color-shimmer-dark) 50%, var(--color-shimmer-light) 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
  }
  
  .shimmer-list {
    margin-top: 20px;
  }
  
  .shimmer-task-item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 15px;
    border: 1px solid var(--color-border-light);
    border-radius: 8px;
    background: var(--color-background);
    margin-bottom: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  }
  
  .shimmer-task-header {
    width: 50%;
    height: 20px;
    border-radius: 4px;
    background: linear-gradient(90deg, var(--color-shimmer-light) 25%, var(--color-shimmer-dark) 50%, var(--color-shimmer-light) 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
  }
  
  .shimmer-task-body {
    width: 100%;
    height: 15px;
    border-radius: 4px;
    background: linear-gradient(90deg, var(--color-shimmer-light) 25%, var(--color-shimmer-dark) 50%, var(--color-shimmer-light) 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
  }
  

@keyframes shimmer {
  0% {
      background-position: -200% 0;
  }
  100% {
      background-position: 200% 0;
  }
}

@media (max-width: 576px) {
  .header-container {
    flex-direction: column;
    gap: 10px;
  }
}